import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import CurrenciesEnum from 'src/components/goaml-enums/currencies-enum/currencies-enum.component';
import FormActions from '../../form-inputs/form-actions.component';
import FormDate from '../../form-inputs/form-date.component';
import FormInput from '../../form-inputs/form-input.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import ReportCodeEnum from '../../goaml-enums/report-code-enum/report-code-enum.component';

import { connect } from 'react-redux';
import { setReport } from '../../../redux/report/report.actions';
import { selectReport } from '../../../redux/report/report.selectors';

const ReportType = ({ name, error, report, setReport }) => {

    const [collapsed, setCollapsed] = React.useState(true)
    const [showElements] = React.useState(true)
    const [reportState, setComponentState] = React.useState({
        "rentity_id": 0,
        "rentity_branch": "",
        "submission_code": "",
        "report_code": 0,
        "entity_reference": null,
        "fiu_ref_number": null,
        "submission_date": "0001-01-01T00:00:00",
        "currency_code_local": 0,
        "reporting_person": null,
        "location": null,
        "reason": "",
        "action": "",
        "transaction": [],
        "activity": null,
        "report_indicators": []
    })

    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [showError, setShowError] = React.useState({ isEmpty: false, value: '' });

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        let myValidation = reportState;
        if (typeof myValidation === 'string' || 'integer' || 'boolean') {
            for (const [key, value] of Object.entries(inputState)) {
                if (e.target.name === key) {
                    console.log("MyValidation: ", myValidation);
                    value === null ? error = true : error = false;
                    val = key;
                }
            }
        }
        setShowError({
            isEmpty: error,
            value: val
        });
    }

    const handleChange = event => {
        console.log(event.target);
        const { name, value } = event.target;
        setComponentState({ ...reportState, [name]: value });
    };

    const handleReactSelectTyeChange = event => {
        let type = event.map((item) => {
            return item.value
        })
        setComponentState({ ...reportState, report_code: type });
    };

    const handleSaveClick = () => {
        if (!reportState.rentity_id || !reportState.rentity_branch || !reportState.entity_reference || !reportState.report_code
            || !reportState.fiu_ref_number || !reportState.submission_date || !reportState.reason || !reportState.action) {
            alert('please complete this field');
        }

        console.log("reportState: ", reportState);
        return setReport({ ...report, ...reportState });
    }

    const handleCancelClick = () => {
        console.log("Hello: ", report);
    }


    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {name}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="4">
                                            <FormInput
                                                label="Entity"
                                                type="number"
                                                name="rentity_id"
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                handleChange={handleChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)}
                                                value={reportState.entity_id}
                                                className={`form-control ${!reportState.rentity_id && touched && 'is-invalid'}${reportState.rentity_id && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput
                                                label="Reporting Entity Branch"
                                                name="rentity_branch"
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                handleChange={handleChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)}
                                                value={reportState.rentity_branch}
                                                className={`form-control ${!reportState.rentity_branch && touched && 'is-invalid'} ${reportState.rentity_branch && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput
                                                label="Reporting Entity Reference"
                                                name="entity_reference"
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                handleChange={handleChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)}
                                                value={reportState.entity_reference}
                                                className={`form-control ${!reportState.entity_reference && touched && 'is-invalid'} ${reportState.entity_reference && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput
                                                label="FIU Ref Number"
                                                name="fiu_ref_number"
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                handleChange={handleChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)}
                                                value={reportState.fiu_ref_number}
                                                className={`form-control ${!reportState.fiu_ref_number && touched && 'is-invalid'} ${reportState.fiu_ref_number && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="8">
                                            <ReportCodeEnum
                                                label="Type*"
                                                name="report_code"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                handleChange={handleReactSelectTyeChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)}
                                                value={reportState.report_code}
                                                className={`form-select ${!reportState.report_code && touched && 'is-invalid'} ${reportState.report_code && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                        <CurrenciesEnum
                                                label="Currency Code" 
                                                name="currency_code_local" 
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus} 
                                                handleChange={handleChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)} 
                                                value={reportState.currency_code_local}  
                                                className={`form-control ${!reportState.currency_code_local && touched && 'is-invalid'} ${reportState.currency_code_local && 'is-valid'}`}
                                        />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormDate
                                                type="date"
                                                label="Submission Date*"
                                                name="submission_date"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                handleChange={handleChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)}
                                                value={reportState.submission_date}
                                                className={`form-select ${!reportState.submission_date && touched && 'is-invalid'} ${reportState.submission_date && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput
                                                label="Action"
                                                name="action"
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                handleChange={handleChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)}
                                                value={reportState.action}
                                                className={`form-control ${!reportState.action && touched && 'is-invalid'} ${reportState.action && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="12">
                                            <FormTextArea
                                                label="Reason for Reporting"
                                                name="reason"
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                handleChange={handleChange}
                                                onKeyUp={(e) => checkInputs(e, reportState)}
                                                value={reportState.reason}
                                                className={`form-select ${!reportState.reason && touched && 'is-invalid'} ${reportState.reason && 'is-valid'}`}
                                            />
                                        </CCol>
                                    </CFormGroup>
                                    <FormActions
                                        saveText="Save Report Type"
                                        handleSaveClick={handleSaveClick}
                                        handleCancelClick={handleCancelClick}
                                    >
                                    </FormActions>
                                </CForm>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

const mapStateToProps = state => ({
    report: selectReport(state)
});

const mapDispatchToProps = dispatch => ({
    setReport: report => dispatch(setReport(report))
});

export default connect(mapStateToProps, mapDispatchToProps)(ReportType);
