import { createSelector } from 'reselect';

const GoAmlReport = state => state.goamlReport;

export const selectReport = createSelector(
    [GoAmlReport],
    goamlReport => goamlReport.report
);

export const selectAllTransactions = createSelector(
    [selectReport],
    report => report.transaction
);

export const selectSingleTransaction = createSelector(
    [selectAllTransactions],
    (transaction, props) => {
        console.log("transaction: ", transaction);
        console.log("props: ", props) 
    }
    //transaction.map(x => x.internal_ref_number == props.transRef)
);

export const makeGetSingleTransaction = () => {
    return createSelector(
        [selectAllTransactions],
        (transaction, props) => {
            return transaction.map(x => x.internal_ref_number == props.transRef)
        }
    )
}