import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import FormActions from '../../form-inputs/form-actions.component';
import FormAmount from '../../form-inputs/form-amount.component';
import FormDate from '../../form-inputs/form-date.component';
import FormInput from '../../form-inputs/form-input.component';
import FormRadioText from '../../form-inputs/form-radio-text.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import ClientTypeEnum from '../../goaml-enums/client-type-enum/client-type-enum.component';
import ConductionTypeEnum from '../../goaml-enums/conduction-type-enum/conduction-type-enum.component';
import transactionFormat from 'src/redux/transaction/transaction.format';

import { connect } from 'react-redux';
import { setReport } from '../../../redux/report/report.actions';
import { selectReport } from '../../../redux/report/report.selectors';
import { RetrieveTransactionFromReport } from '../../../redux/goaml-utils/goaml-utils';

const TransactionInfo = ({ label, name, transRef, report, reportTransaction, setReport, handleSaveInfoClick, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(true)
    const [showElements, setShowElements] = React.useState(true)
    let radioItems = ["Yes", "No"];
    const [check, setCheck] = React.useState(false);
    const onChecked = (e) => {
        const { name, value } = e.target;
        console.log({ name, value })
        setCheck({ [name]: value })
    }

    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [showError, setShowError] = React.useState({ isEmpty: false, value: '' });

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        let myValidation;
        if (typeof myValidation === 'string' || 'integer' || 'boolean') {
            for (const [key, value] of Object.entries(inputState)) {
                if (e.target.name === key) {
                    value === null ? error = true : error = false;
                    val = key;
                }
            }
        }
        setShowError({
            isEmpty: error,
            value: val
        });
    }
    const [transaction, setTransactionState] = React.useState({
        detail: transactionFormat,
        txn_from: "",
        txn_to: ""
    })

    console.log('error', showError)

    const handleChange = event => {
        const { name, value } = event.target;
        if (name == "txn_from" || name == "txn_to")
            setTransactionState({ ...transaction, [name]: value });
        else
            setTransactionState({ ...transaction, detail: { ...transaction.detail, [name]: value } });
    };

    const handleSaveClick = () => {

        console.log("transaction: ", transaction);

        if (!transaction.txn_from || !transaction.txn_to) {
            alert('please complete the sender and/or receiver field');
        }
        else if (!transaction.detail.transactionnumber || !transaction.detail.transmode_code || !transaction.detail.internal_ref_number 
            || !transaction.detail.date_transaction || !transaction.detail.transaction_description 
            || !transaction.detail.transaction_location || !transaction.detail.teller 
            || !transaction.detail.authorized || !transaction.detail.date_posting || !transaction.detail.amount_local) {
            alert('please complete this field');
        }
        const existingTxn = RetrieveTransactionFromReport(report, transaction.internal_ref_number);

        if (existingTxn) {
            setTransactionState({ ...transaction.detail, ...existingTxn });
            let reportTxns = report.transaction.map(reportTransaction =>
                reportTransaction.internal_ref_number === transaction.detail.internal_ref_number
                    ? transaction.detail
                    : reportTransaction
            );
            handleSaveInfoClick(reportTxns, transaction);
        }
        else {
            let reportTxns = report.transaction;
            reportTxns.push(transaction.detail)
            handleSaveInfoClick(reportTxns, transaction);
        }
    }

    const handleCancelClick = () => {
        console.log("Hello: ", report);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {label}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm method="post" className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="4">
                                            <FormInput
                                                type="number"
                                                label="Number*"
                                                placeholder="Enter ..."
                                                name="transactionnumber"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                handleChange={handleChange}
                                                value={transaction.detail.transactionnumber}
                                                className={`form-control ${!transaction.detail.transactionnumber && touched && 'is-invalid'} ${transaction.detail.transactionnumber && 'is-valid'}`}
                                            />
                                            <FormInput
                                               label="TransMode Comment" 
                                                name="transmode_comment" 
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                value={transaction.detail.transmode_comment}
                                                handleChange={handleChange}
                                                className={`form-control ${!transaction.detail.transmode_comment && touched && 'is-invalid'} ${transaction.detail.transmode_comment && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput
                                                label="Internal Reference Number"
                                                placeholder="Enter ..."
                                                name="internal_ref_number"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                value={transaction.detail.internal_ref_number}
                                                handleChange={handleChange}
                                                className={`form-control ${!transaction.detail.internal_ref_number && touched && 'is-invalid'} ${transaction.detail.internal_ref_number && 'is-valid'}`}
                                            />
                                            <FormAmount
                                                label="Local Amount*"
                                                name="amount_local"
                                                placeholder="0.00"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                value={transaction.detail.amount_local}
                                                handleChange={handleChange}
                                                className={`form-control ${!transaction.detail.amount_local && touched && 'is-invalid'} ${transaction.detail.amount_local && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <ConductionTypeEnum
                                                label="TransMode Code*"
                                                name="transmode_code"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                handleChange={handleChange}
                                                value={transaction.detail.transmode_code}
                                                className={`form-control ${!transaction.detail.transmode_code && touched && 'is-invalid'} ${transaction.detail.transmode_code && 'is-valid'}`}
                                            />
                                            <FormDate
                                                type="date"
                                                label="Trans Date*"
                                                name="date_transaction"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                value={transaction.detail.date_transaction}
                                                handleChange={handleChange}
                                                className={`form-control ${!transaction.detail.date_transaction && touched && 'is-invalid'} ${transaction.detail.date_transaction && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormDate
                                                type="date"
                                                label="Post Date*"
                                                name="date_posting"
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                value={transaction.detail.date_posting}
                                                handleChange={handleChange}
                                                className={`form-control ${!transaction.detail.date_posting && touched && 'is-invalid'} ${transaction.detail.date_posting && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormDate 
                                                type="date"
                                                label="Value Date*" 
                                                name="value_date" 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction)} 
                                                value={transaction.value_date} 
                                                handleChange={handleChange} 
                                                className={`form-control ${!transaction.value_date && touched && 'is-invalid'} ${transaction.value_date && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput 
                                                label="Teller" 
                                                placeholder="Enter ..." 
                                                name="teller" 
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                value={transaction.detail.teller}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                handleChange={handleChange}
                                                className={`form-control ${!transaction.detail.teller && touched && 'is-invalid'} ${transaction.detail.teller && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormInput
                                                label="Authorised"
                                                name="authorized"
                                                placeholder="Enter ..."
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                value={transaction.detail.authorized}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                handleChange={handleChange}
                                                className={`form-control ${!transaction.detail.authorized && touched && 'is-invalid'} ${transaction.detail.authorized && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="12">
                                            <CRow>
                                                <CCol xs="6">
                                                    <FormInput
                                                        label="Location*"
                                                        name="transaction_location"
                                                        placeholder="Enter ..."
                                                        onBlur={onBlur}
                                                        onFocus={onFocus}
                                                        onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                        value={transaction.detail.transaction_location}
                                                        handleChange={handleChange}
                                                        className={`form-control ${!transaction.detail.transaction_location && touched && 'is-invalid'} ${transaction.detail.transaction_location && 'is-valid'}`}
                                                    />
                                                </CCol>
                                                <CCol xs="6">
                                                    <FormRadioText
                                                        label="Late Deposit?"
                                                        name="late_deposit"
                                                        width="6"
                                                        items={["Yes", "No"]}
                                                        handleChange={onChecked}
                                                        defaultValue={transaction.late_deposit}
                                                    />
                                                </CCol>
                                            </CRow>
                                        </CCol>
                                        <CCol xs="12">
                                            <CRow>
                                                <CCol xs="6">
                                                    <ClientTypeEnum
                                                        handleChange={handleChange}
                                                        label="From Party?*"
                                                        name="txn_from"
                                                        value={transaction.txn_from}
                                                        onFocus={onFocus}
                                                        onBlur={onBlur}
                                                        onKeyUp={(e) => checkInputs(e, transaction)}
                                                        className={`form-control ${!transaction.txn_from && touched && 'is-invalid'} ${transaction.txn_from && 'is-valid'}`}
                                                    />
                                                </CCol>
                                                <CCol xs="6">
                                                    <ClientTypeEnum
                                                        handleChange={handleChange}
                                                        label="To Party?*"
                                                        name="txn_to"
                                                        value={transaction.txn_to}
                                                        onFocus={onFocus}
                                                        onBlur={onBlur}
                                                        onKeyUp={(e) => checkInputs(e, transaction)}
                                                        className={`form-control ${!transaction.txn_to && touched && 'is-invalid'} ${transaction.txn_to && 'is-valid'}`}
                                                    />
                                                </CCol>
                                            </CRow>
                                        </CCol>
                                        <CCol xs="12">
                                            <FormTextArea
                                                label="Description"
                                                name="transaction_description"
                                                value={transaction.detail.transaction_description}
                                                onBlur={onBlur}
                                                onFocus={onFocus}
                                                onKeyUp={(e) => checkInputs(e, transaction.detail)}
                                                handleChange={handleChange}
                                                placeholder="Enter ..."
                                                className={`form-control ${!transaction.detail.transaction_description && touched && 'is-invalid'} ${transaction.detail.transaction_description && 'is-valid'}`}
                                            />
                                        </CCol>
                                    </CFormGroup>
                                    <FormActions
                                        type="submit" class="btn btn-primary"
                                        saveText="Save Overview"
                                        handleSaveClick={handleSaveClick}
                                        handleCancelClick={handleCancelClick}>
                                    </FormActions>
                                </CForm>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}


const makeMapStateToProps = () => {
    const mapStateToProps = (state, props) => {
        return {
            report: selectReport(state)
        }
    }
    return mapStateToProps
}
const mapDispatchToProps = dispatch => ({
    setReport: report => dispatch(setReport(report))
});

export default connect(makeMapStateToProps, mapDispatchToProps)(TransactionInfo);