import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import { connect } from 'react-redux';
import FormAmount from 'src/components/form-inputs/form-amount.component';
import { setReport } from '../../../redux/report/report.actions';
import { selectReport } from '../../../redux/report/report.selectors';
import FormActions from '../../form-inputs/form-actions.component';
import FormInput from '../../form-inputs/form-input.component';
import CountryCodeEnum from '../../goaml-enums/country-code-enum/country-code-enum.component';
import CurrenciesEnum from '../../goaml-enums/currencies-enum/currencies-enum.component';
import FundsTypeEnum from '../../goaml-enums/funds-type-enum/funds-type-enum.component';
import TransactionPerson from '../transaction-person/transaction-person.component';
import TransactionAccount from '../transaction-account/transaction-account.components';
import TransactionEntity from '../transaction-entity/transaction-entity.components';
import TransactionsPartyTypes from '../transaction-party/transaction-party.types'

import t_from_my_client from '../../../redux/transaction-party/from-my-client.format'

const TransactionFromMyClient = ({ name, transRef, clientType, partyType, handleSavePartyInfo, report, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(true)
    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [collapseMulti, setCollapseMulti] = React.useState([false, false, false, false, false, false])
    let [showElements, setShowElements] = React.useState(true)
    const [transpartyInfo, setTranspartyInfo] = React.useState(t_from_my_client)

    const [showError, setShowError] = React.useState({ isEmpty: false, value: '' });

    const checkInputs = (e, inputState) => {
        let error = false;
        let val = {};
        let myValidation;
        if (typeof myValidation === 'string' || 'integer' || 'boolean') {
            for (const [key, value] of Object.entries(inputState)) {
                if (e.target.name === key) {
                    value === null ? error = true : error = false;
                    val = key;
                }
            }
        }
        setShowError({
            isEmpty: error,
            value: val
        });
    }

    console.log('error', showError)

    const toggleMulti = (type) => {
        let newCollapse = collapseMulti.slice()
        switch (type) {
            case "person-conductor":
                newCollapse[0] = !collapseMulti[0];
                newCollapse[1] = false;
                newCollapse[2] = false;
                newCollapse[3] = false;
                break;
            case "person-client":
                newCollapse[1] = !collapseMulti[1];
                newCollapse[0] = false;
                newCollapse[2] = false;
                newCollapse[3] = false;
                break;
            case "account-client":
                newCollapse[2] = !collapseMulti[2];
                newCollapse[0] = false;
                newCollapse[1] = false;
                newCollapse[3] = false;
                break;
            case "entity-client":
                newCollapse[3] = !collapseMulti[3];
                newCollapse[0] = false;
                newCollapse[1] = false;
                newCollapse[2] = false;
                break;
            default:
        }
        setCollapseMulti(newCollapse)
    }

    const handleChange = event => {
        const { name, value } = event.target;
        if (name == "foreign_currency_code" || name == "foreign_exchange_rate" || name == "foreign_amount")
            setTranspartyInfo({ ...transpartyInfo, from_foreign_currency: { ...transpartyInfo.from_foreign_currency, [name]: value } });
        else
            setTranspartyInfo({ ...transpartyInfo, [name]: value });
    };

    const handleSaveConductor = (conductor) => {
        setTranspartyInfo({ ...transpartyInfo, t_conductor: conductor });
        handleSaveClick();
    }

    const handleSaveEntity = (entity) => {
        setTranspartyInfo({ ...transpartyInfo, from_entity: entity });
        handleSaveClick();
    }

    const handleSaveAccount = (account) => {
        setTranspartyInfo({ ...transpartyInfo, from_account: account });
        handleSaveClick();
    }

    const handleSavePerson = (person) => {
        setTranspartyInfo({ ...transpartyInfo, from_person: person });
        handleSaveClick();
    }

    const handleSaveClick = () => {
        if (!transpartyInfo.from_funds_code || !transpartyInfo.from_funds_comment || !transpartyInfo.from_country
            || !transpartyInfo.from_foreign_currency.foreign_currency_code) {
            alert('please complete this field')
        }
        handleSavePartyInfo(transpartyInfo);
    }

    const handleCancelClick = () => {
        console.log("Hello: ", report);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {name}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="6">
                                            <FundsTypeEnum
                                                label="Funds Code*"
                                                name="from_funds_code"
                                                handleChange={handleChange}
                                                value={transpartyInfo.from_funds_code}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.from_funds_code && touched && 'is-invalid'} ${transpartyInfo.from_funds_code && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="6">
                                            <FormInput
                                                label="Funds Comment"
                                                name="from_funds_comment"
                                                placeholder="Enter ..."
                                                handleChange={handleChange}
                                                value={transpartyInfo.from_funds_comment}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                className={`from-control ${!transpartyInfo.from_funds_comment && touched && 'is-invalid'} ${transpartyInfo.from_funds_comment && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="6">
                                            <CountryCodeEnum
                                                handleChange={handleChange}
                                                label="Country*"
                                                name="from_country"
                                                value={transpartyInfo.from_country}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.from_country && touched && 'is-invalid'} ${transpartyInfo.from_country && touched && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="6">
                                            
                                        </CCol>
                                        <CCol xs="4">
                                            <CurrenciesEnum
                                                handleChange={handleChange}
                                                label="Currency Code*"
                                                name="foreign_currency_code"
                                                value={transpartyInfo.from_foreign_currency.foreign_currency_code}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.from_foreign_currency.foreign_currency_code && touched && 'is-invalid'} ${transpartyInfo.from_foreign_currency.foreign_currency_code && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormAmount
                                                handleChange={handleChange}
                                                label="Exchange Rate*"
                                                name="foreign_exchange_rate"
                                                value={transpartyInfo.from_foreign_currency.foreign_exchange_rate}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.from_foreign_currency.foreign_exchange_rate && touched && 'is-invalid'} ${transpartyInfo.from_foreign_currency.foreign_exchange_rate && 'is-valid'}`}
                                            />
                                        </CCol>
                                        <CCol xs="4">
                                            <FormAmount
                                                handleChange={handleChange}
                                                label="Amount*"
                                                name="foreign_amount"
                                                value={transpartyInfo.from_foreign_currency.foreign_amount}
                                                onFocus={onFocus}
                                                onBlur={onBlur}
                                                onKeyUp={(e) => checkInputs(e, transpartyInfo)}
                                                className={`from-control ${!transpartyInfo.from_foreign_currency.foreign_amount && touched && 'is-invalid'} ${transpartyInfo.from_foreign_currency.foreign_amount && 'is-valid'} `}
                                            />
                                        </CCol>
                                        <CCol xs="12">
                                            <p>
                                                <button type="button" onClick={() => { toggleMulti('person-conductor') }}
                                                    class="btn btn-link">Add Conductor</button>
                                            </p>
                                            <row>
                                                <CCollapse show={collapseMulti[0]}>
                                                    <TransactionPerson OnSave={handleSaveConductor} transRef={transRef} partyType={partyType} label="Add a Conductor" name="conductor" alias="Conductor" ></TransactionPerson>
                                                </CCollapse>
                                            </row>
                                        </CCol>
                                        <CCol xs="12">
                                            <p>
                                                <CButton color="primary" onClick={() => { toggleMulti('person-client') }}
                                                    class="btn btn-link">Add Person</CButton>{' '}
                                                <CButton color="primary" onClick={() => { toggleMulti('account-client') }}
                                                    class="btn btn-link">Add Account</CButton>{' '}
                                                <CButton color="primary" onClick={() => { toggleMulti('entity-client') }}
                                                    class="btn btn-link">Add Entity</CButton>{' '}
                                            </p>
                                            <CRow>
                                                <CCol className="col-sm-12">
                                                    <CCollapse show={collapseMulti[1]}>
                                                        <TransactionPerson OnSave={handleSavePerson} partyType={TransactionsPartyTypes.PERSON_CLIENT} transRef={transRef} label="Transaction Person" name="Person" alias="Person"></TransactionPerson>
                                                    </CCollapse>
                                                </CCol>
                                                <CCol className="col-sm-12">
                                                    <CCollapse show={collapseMulti[2]}>
                                                        <TransactionAccount OnSave={handleSaveAccount} partyType={TransactionsPartyTypes.ACCOUNT_CLIENT} transRef={transRef} name="TransactionAccount" label="Transaction Account"></TransactionAccount>
                                                    </CCollapse>
                                                </CCol>
                                                <CCol className="col-sm-12">
                                                    <CCollapse show={collapseMulti[3]}>
                                                        <TransactionEntity OnSave={handleSaveEntity} partyType={TransactionsPartyTypes.ENTITY_CLIENT} transRef={transRef} name="TransactionEntity" label="Transaction Entity"></TransactionEntity>
                                                    </CCollapse>
                                                </CCol>
                                            </CRow>
                                        </CCol>
                                    </CFormGroup>
                                </CForm>
                                <FormActions
                                    saveText="Save Party Details"
                                    handleSaveClick={handleSaveClick}
                                    handleCancelClick={handleCancelClick}>
                                </FormActions>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

const mapStateToProps = state => ({
    report: selectReport(state)
});

export default connect(mapStateToProps, null)(TransactionFromMyClient);