import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component'

class ClientTypeEnum extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            options: [
                { value: 'ACCOUNT_CLIENT', description: 'My Client (Account)' },
                { value: 'PERSON_CLIENT', description: 'My Client (Person)' },
                { value: 'ENTITY_CLIENT', description: 'My Client (Entity)' },
                { value: 'ACCOUNT_NOT_CLIENT', description: 'Not My Client (Account)' },
                { value: 'PERSON_NOT_CLIENT', description: 'Not My Client (Person)' },
                { value: 'ENTITY_NOT_CLIENT', description: 'Not My Client (Entity)' }
            ]
        }
    }

    render() {
        const { options } = this.state;
        const { name, label, handleChange } = this.props;

        return (
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default ClientTypeEnum;