import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import TransactionInfo from '../transaction-info/transaction-info.components';
import TransactionItem from '../transaction-item/transaction-item.components';
import TransactionParty from '../transaction-party/transaction-party.component';
import TransactionsPartyTypes from '../transaction-party/transaction-party.types';

import { connect } from 'react-redux';
import { setReport, addTransactionToReport } from '../../../redux/report/report.actions';
import { selectReport } from '../../../redux/report/report.selectors';


const TransactionTemplate = ({ name, report, transaction, addReportTransaction, ...otherProps }) => {

    let [collapsed, setCollapsed] = React.useState(false)
    let [showElements, setShowElements] = React.useState(true)

    let [transitState, setTransitState] = React.useState({
        transRef: null,
        txn_from: null,
        txn_to: null
    })

    const handleTransInfoSaveClick = (reportTxns, transaction) => {
        setTransitState({ ...transitState, transRef: transaction.detail.internal_ref_number, 
            txn_from: transaction.txn_from, txn_to: transaction.txn_to });
        addReportTransaction(reportTxns);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {name}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup row>
                                        <CCol xs="12">
                                            <TransactionInfo handleSaveInfoClick={handleTransInfoSaveClick} transRef={transitState.transRef} label="Transaction Overview" name="TransactionOverview" />
                                        </CCol>
                                        <CCol xs="12">
                                            <TransactionParty transRef={transitState.transRef} from={transitState.txn_from} label="From Party" name="FromParty" displayPartyType={TransactionsPartyTypes.FROM} />
                                        </CCol>
                                        <CCol xs="12">
                                            <TransactionParty transRef={transitState.transRef} to={transitState.txn_to} label="To Party" name="ToParty" displayPartyType={TransactionsPartyTypes.TO} />
                                        </CCol>
                                        <CCol xs="12">
                                            <TransactionItem transRef={transitState.transRef} label="Transaction Item" name="goods_services"></TransactionItem>
                                        </CCol>
                                    </CFormGroup>
                                </CForm>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

const mapStateToProps = (state, props) => ({
    report: selectReport(state)
});

const mapDispatchToProps = dispatch => ({
    setReport: report => dispatch(setReport(report)),
    addReportTransaction: report => dispatch(addTransactionToReport(report))
});

export default connect(mapStateToProps, mapDispatchToProps)(TransactionTemplate);