import React from 'react';
import XLSX from 'xlsx';
import { make_cols } from '../goaml-core/MakeColumns';
import { SheetJSFT } from '../goaml-core/types';
import GoAmlExcelUpload from './goaml-excel-upload.components';

class GoamlExcelFileReader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fileName: "Choose file...",
      file: {},
      data: [],
      cols: []
    }
  }

  handleChange = (e) => {
    const files = e.target.files;
    if (files && files[0]) this.setState({ file: files[0], fileName: files[0].name });
  };

  handleFile = () => {
    /* Boilerplate to set up FileReader */
    const reader = new FileReader();
    const rABS = !!reader.readAsBinaryString;

    reader.onload = (e) => {
      /* Parse data */
      const bstr = e.target.result;
      const wb = XLSX.read(bstr, { type: rABS ? 'binary' : 'array', bookVBA: true });
      /* Get first worksheet */
      const wsname = wb.SheetNames[0];
      const ws = wb.Sheets[wsname];
      /* Convert array of arrays */
      const data = XLSX.utils.sheet_to_json(ws);
      /* Update state */
      this.setState({ data: data, cols: make_cols(ws['!ref']) }, () => {
        console.log(JSON.stringify(this.state.data, null, 2));
      });
    };

    if (rABS) {
      reader.readAsBinaryString(this.state.file);
    } else {
      reader.readAsArrayBuffer(this.state.file);
    };
  }

  render() {
    const { name } = this.props;
    return (
      <GoAmlExcelUpload
        name={name}
        id="file"
        fileName={this.state.fileName}
        accept=".xls,.xlsx, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
        handleChange={this.handleChange}
        handleFile={this.handleFile}></GoAmlExcelUpload>
    )
  }
}

export default GoamlExcelFileReader;