import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component';

class EntityPersonRoleEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {description:'select'},
                {value: 'F', description:'Fudiciary Agent'},
                {value: 'N', description:'Nominee'},
                {value: 'P', description:'Pertners'},
                {value: 'B', description:'Shareholders'},
                {value: 'C', description:'Signatory'},
                {value: 'S', description:'Sole Proprietor'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default EntityPersonRoleEnum;