import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import { connect } from 'react-redux';
import FormAmount from 'src/components/form-inputs/form-amount.component';
import transactionGoodAndService from 'src/redux/transaction-item/transaction-item.format';
import { setReport } from '../../../redux/report/report.actions';
import { selectReport } from '../../../redux/report/report.selectors';
import FormActions from '../../form-inputs/form-actions.component';
import FormDate from '../../form-inputs/form-date.component';
import FormInput from '../../form-inputs/form-input.component';
import FormTextArea from '../../form-inputs/form-textarea-component';
import CurrenciesEnum from '../../goaml-enums/currencies-enum/currencies-enum.component';
import TransactionItemStatusEnum from '../../goaml-enums/transaction-item-status-enum/transaction-item-status-enum.component';
import TransactionItemTypeEnum from '../../goaml-enums/transaction-item-type-enum/transaction-item-type-enum.component';

const TransactionItem = ({ reportType, label, name, setReportType, handleSaveInfoClick, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    let radioItems = ["Yes", "No"];
    const [transaction, setTransactionState] = React.useState(transactionGoodAndService);

    const [touched, setTouched] = React.useState(false);
    const onFocus = () => setTouched(true)
    const onBlur = () => setTouched(false)

    const [showError, setShowError] = React.useState({isEmpty: false, value: ''});

    const checkData = (e, inputState) => {
        let error = true;
        let val = {};
        let myValidation;
        if (typeof myValidation === 'string' || 'integer' || 'boolean'){
            for (const [key, value] of Object.entries(inputState)) {
                if (e.target.name === key) {
                    value === null ? error = true : error = false; 
                    val = key;
                }
            }
        }
        setShowError({
            isEmpty: error,
            value: val
         });
    }

    console.log('error', showError)
    
    const handleChange = event => {
        // console.log({event.target});
        const { name, value } = event.target;
        setTransactionState({ ...transaction, [name]: value });
    };


    const handleSaveClick = () => {
        // if (!transaction.item_type || !transaction.previous_register_to || !transaction.status_code || !transaction.size || !transaction.registration_number ||
        //     !transaction.item_make || !transaction.disposed_value || !transaction.size_UOM || !transaction.identification_number || !transaction.description ||
        //     !transaction.estimate_value || !transaction.currency_code || !transaction.registration_date || !transaction.status_commets) {
        //     alert('please fill this field');
        // }
        return setReportType({...reportType, ...transaction});
    }

    const handleCancelClick = () => {
        console.log("Hello: ", reportType);
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                    {label}
                    <div className="card-header-actions">
                        <CButton 
                        color="link" 
                        className="card-header-action btn-minimize" 
                        onClick={() => setCollapsed(!collapsed)}
                        >
                        <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                        </CButton>
                    </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm  method="post" className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="4">
                                    <TransactionItemTypeEnum 
                                        label="Item Type*" 
                                        name="item_Type"
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        className={`form-control ${!transaction.item_type && touched && 'is-invalid'} ${transaction.item_type && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Previously Registered To" 
                                        name="previous_register_to" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.previous_register_to}
                                        className={`form-control ${!transaction.previous_register_to && touched && 'is-invalid'} ${transaction.presently_register_to && 'is-valid'}`}
                                     />
                                    <TransactionItemStatusEnum 
                                        label="Status Code" 
                                        name="status_code"
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)}
                                        value={transaction.status_code}
                                        className={`form-control ${!transaction.status_code && touched && 'is-invalid'} ${transaction.status_code && 'is-valid'}`}
                                    />
                                    <FormAmount 
                                        label="Size" 
                                        name="size"
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)}
                                        value={transaction.size}
                                        className={`form-control ${!transaction.size && touched && 'is-invalid'} ${transaction.size && 'is-valid'}`}
                                    />
                                    <FormInput
                                        onBlur={onBlur}
                                        onFocus={onFocus} 
                                        label="Registration Number" 
                                        name="registration_number"
                                        placeholder="Enter ..." 
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.registration_number}
                                        className={`form-control ${!transaction.registration_number && touched && 'is-invalid'} ${transaction.registration_number && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Item Make*" 
                                        name="item_make"
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.item_make}
                                        className={`form-control ${!transaction.item_make && touched && 'is-invalid'} ${transaction.item_make && 'is-valid'}`}
                                    />
                                    <FormAmount 
                                        label="Disposed Value"
                                        name="disposed_value"
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.disposed_value}
                                        className={`form-control ${!transaction.disposed_value && touched && 'is-invalid'} ${transaction.disposed_value && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Size UOM" 
                                        name="size_UOM" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.size_UOM}
                                        className={`form-control ${!transaction.size_UOM && touched && 'is-invalid'} ${transaction.size_UOM && 'is-valid'}`}
                                    />
                                    <FormInput 
                                        label="Identification Number" 
                                        name="identification_number" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.identification_number}
                                        className={`form-control ${!transaction.identification_number && touched && 'is-invalid'} ${transaction.identification_number && 'is-valid'}`}
                                    />
                                    <FormTextArea 
                                        label="Comments" 
                                        name="comments" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.comments} 
                                        className={`form-control ${!transaction.comments && touched && 'is-invalid'} ${transaction.comments && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <FormInput 
                                        label="Description*" 
                                        name="description" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.description}
                                        className={`form-control ${!transaction.description && touched && 'is-invalid'} ${transaction.description && 'is-valid'}`}
                                    />
                                    <FormAmount 
                                        label="Estimated Value" 
                                        name="estimate_value" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.estimate_value}
                                        className={`form-control ${!transaction.estimate_value && touched && 'is-invalid'} ${transaction.estimate_value && 'is-valid'}`}
                                    />
                                    <CurrenciesEnum 
                                        label="Currency Code*" 
                                        name="currency_code"
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.currency_code} 
                                        className={`form-control ${!transaction.currency_code && touched && 'is-invalid'} ${transaction.currency_code && 'is-valid'}`}
                                    />
                                    <FormDate 
                                        label="Registration Date*" 
                                        name="registration_date"
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.registration_date} 
                                        className={`form-control ${!transaction.registration_date && touched && 'is-invalid'} ${transaction.registration_date && 'is-valid'}`}
                                    />
                                </CCol>
                                <CCol xs="12">
                                    <FormTextArea 
                                        label="Status Comments" 
                                        name="status_comments" 
                                        placeholder="Enter ..."
                                        onBlur={onBlur}
                                        onFocus={onFocus}
                                        handleChange={handleChange} 
                                        onKeyUp={(e) => checkData(e, transaction)} 
                                        value={transaction.status_commets} 
                                        className={`form-control ${!transaction.status_commets && touched && 'is-invalid'} ${transaction.status_commets && 'is-valid'}`}
                                    />
                                </CCol>
                            </CFormGroup>
                            <FormActions 
                                type="submit" class="btn btn-primary"
                                saveText="Save Item" 
                                handleSaveClick={handleSaveClick} 
                                handleCancelClick={handleCancelClick}>
                            </FormActions>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

const mapStateToProps = state => ({
    reportType: selectReport(state)
});

const mapDispatchToProps = dispatch => ({
    setReportType: reportType => dispatch(setReport(reportType))
});

export default connect(mapStateToProps, mapDispatchToProps)(TransactionItem);
