import React from 'react';
import {
    CLabel,
    CCard,
    CCardBody,
    CCol,
    CFade,
    CRow,
    CNav,
    CNavItem,
    CNavLink,
    CTabContent,
    CTabPane,
    CTabs
} from '@coreui/react'

import TransactionFrom from '../transaction-party/party-from.component'
import TransactionFromMyClient from '../transaction-party/party-from-my-client.component'
import TransactionTo from '../transaction-party/party-to.component'
import TransactionToMyClient from '../transaction-party/party-to-my-client.component'
import TransactionsPartyTypes from '../transaction-party/transaction-party.types'

import { connect } from 'react-redux';
import { selectReport } from '../../../redux/report/report.selectors';
import { addTFrom } from '../../../redux/report/report.actions';
import { addTFromMyClient } from '../../../redux/report/report.actions';
import { addTTo } from '../../../redux/report/report.actions';
import { addTToMyClient } from '../../../redux/report/report.actions';

import { RetrieveTransactionFromReport } from '../../../redux/goaml-utils/goaml-utils';

const TransactionParty = ({ name, label, from, to, transRef, report, addTFrom, addTTo, addTFromMyClient, addTToMyClient, displayPartyType, ...otherProps }) => {

    let [showElements, setShowElements] = React.useState(true);

    const SaveTransactionFromMyClient = (transparty) => {
        const existingTxn = RetrieveTransactionFromReport(report, transRef);
        if (existingTxn) {
            addTFromMyClient(report, existingTxn.internal_ref_number, transparty);
        }
        else{
            alert("Transaction with Internal Ref Number '", transRef, "' cannot be found. Save failed!");
        }
    }

    const SaveTransactionFrom = (transparty) => {
        const existingTxn = RetrieveTransactionFromReport(report, transRef);
        if (existingTxn) {
            addTFrom(report, existingTxn.internal_ref_number, transparty);
        }
        else{
            alert("Transaction with Internal Ref Number '", transRef, "' cannot be found. Save failed!");
        }
    }

    const SaveTransactionToMyClient = (transparty) => {
        const existingTxn = RetrieveTransactionFromReport(report, transRef);
        if (existingTxn) {
            addTToMyClient(report, existingTxn.internal_ref_number, transparty);
        }
        else{
            alert("Transaction with Internal Ref Number '", transRef, "' cannot be found. Save failed!");
        }
    }

    const SaveTransactionTo = (transparty) => {
        const existingTxn = RetrieveTransactionFromReport(report, transRef);
        if (existingTxn) {
            addTTo(report, existingTxn.internal_ref_number, transparty);
        }
        else{
            alert("Transaction with Internal Ref Number '", transRef, "' cannot be found. Save failed!");
        }
    }

    return (
        <CRow>
            <CCol xs="12">
                {label ? (
                    <CLabel htmlFor={name}>{label}</CLabel>
                ) : null}
                <div className="row">
                    <CCol xs="12">
                        <CTabs activeTab="myClient">
                            <CNav variant="tabs">
                                <CNavItem>
                                    <CNavLink data-tab="myClient">
                                        Party Details
                                </CNavLink>
                                </CNavItem>
                            </CNav>
                            <CTabContent>
                                <CTabPane data-tab="myClient">
                                    <CFade timeout={300} in={showElements} unmountOnExit={true}>
                                        <CCard>
                                            <CCardBody>
                                                {displayPartyType == TransactionsPartyTypes.FROM ?
                                                    (from == TransactionsPartyTypes.ACCOUNT_CLIENT
                                                        || from == TransactionsPartyTypes.PERSON_CLIENT
                                                        || from == TransactionsPartyTypes.ENTITY_CLIENT) ?
                                                        <TransactionFromMyClient transRef={transRef} clientType={from} partyType={TransactionsPartyTypes.MY_CLIENT} handleSavePartyInfo={SaveTransactionFromMyClient} name="Expand and Fill From My Clent Overview" />
                                                        :
                                                        <TransactionFrom transRef={transRef} clientType={from} partyType={TransactionsPartyTypes.MY_CLIENT} handleSavePartyInfo={SaveTransactionFrom} name="Expand and Fill From Clent Overview" />
                                                    :
                                                    (to == TransactionsPartyTypes.ACCOUNT_CLIENT
                                                        || to == TransactionsPartyTypes.PERSON_CLIENT
                                                        || to == TransactionsPartyTypes.ENTITY_CLIENT) ?
                                                        <TransactionToMyClient transRef={transRef} clientType={to} partyType={TransactionsPartyTypes.MY_CLIENT} handleSavePartyInfo={SaveTransactionToMyClient} name="Expand and Fill To My Clent Overview" />
                                                        :
                                                        <TransactionTo transRef={transRef} clientType={to} partyType={TransactionsPartyTypes.MY_CLIENT} handleSavePartyInfo={SaveTransactionTo} name="Expand and Fill To Clent Overview" />
                                                }
                                            </CCardBody>
                                        </CCard>
                                    </CFade>
                                </CTabPane>
                            </CTabContent>
                        </CTabs>
                    </CCol>
                </div>
            </CCol>
        </CRow>
    );
}

const makeMapStateToProps = () => {
    const mapStateToProps = (state, props) => {
        return {
            report: selectReport(state)
        }
    }
    return mapStateToProps
}
const mapDispatchToProps = dispatch => ({
    addTFrom: (report, internal_ref_number, item) => dispatch(addTFrom(report, internal_ref_number, item)),
    addTTo: (report, internal_ref_number, item) => dispatch(addTTo(report, internal_ref_number, item)),
    addTFromMyClient: (report, internal_ref_number, item) => dispatch(addTFromMyClient(report, internal_ref_number, item)),
    addTToMyClient: (report, internal_ref_number, item) => dispatch(addTToMyClient(report, internal_ref_number, item)),
});

export default connect(makeMapStateToProps, mapDispatchToProps)(TransactionParty);