import React from 'react';
import FormDynamicSelect from '../../form-inputs/form-dynamic-select.component';

class AccountTypeEnum extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            options : [
                {description:'select'},
                {value: 'A', description:'Savings'},
                {value: 'U', description:'Transit Account'},
                {value: 'D', description:'Current Business'},
                {value: 'E', description:'Current Personal'},
                {value: 'I', description:'Fixed Deposit'},
                {value: 'L', description:'Trading Account'},
                {value: 'O', description:'Export Proceed Dorm'},
                {value: 'C', description:'Call Deposit'},
                {value: 'T', description:'Internal Account'},
                {value: 'B', description:'Trust Account'},
                {value: 'Y', description:'Escrow Account'},
                {value: 'P', description:'Dormicilliary Account'}
            ]
        }
    }

    render(){
        const { options } = this.state;
        const { name, label, handleChange } = this.props;
        
        return(
            <FormDynamicSelect label={label} name={name} options={options} handleChange={handleChange} />
        )

    }
}

export default AccountTypeEnum;