import React from 'react'
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CLabel,
    CInputFile,
    CRow,
    CFade,
    CForm,
    CFormGroup
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import FormActions from '../form-inputs/form-actions.component';

const GoAmlExcelUpload = ({ id, name, accept, handleChange, handleFile, fileName, ...otherProps }) => {

    let [collapsed, setCollapsed] = React.useState(true);
    const [showElements] = React.useState(true)

    const handleCancelClick = () => {
        console.log("Hello: ");
    }

    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                    <CCard>
                        <CCardHeader>
                            {name}
                            <div className="card-header-actions">
                                <CButton
                                    color="link"
                                    className="card-header-action btn-minimize"
                                    onClick={() => setCollapsed(!collapsed)}
                                >
                                    <CIcon name={collapsed ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                                </CButton>
                            </div>
                        </CCardHeader>
                        <CCollapse show={collapsed} timeout={1000}>
                            <CCardBody>
                                <CForm className="form-horizontal">
                                    <CFormGroup>
                                        <CCol xs="12">
                                            <CInputFile custom
                                                id={id}
                                                className="form-control"
                                                name={name}
                                                accept={accept}
                                                onChange={handleChange} />
                                            <CLabel htmlFor="custom-file-input" variant="custom-file">
                                                {fileName}</CLabel>
                                        </CCol>
                                    </CFormGroup>
                                    <FormActions
                                        saveText="Upload Excel File"
                                        handleSaveClick={handleFile}
                                        handleCancelClick={handleCancelClick}
                                    >
                                    </FormActions>
                                </CForm>
                            </CCardBody>
                        </CCollapse>
                    </CCard>
                </CFade>
            </CCol>
        </CRow>
    )
}

export default GoAmlExcelUpload
