import ReportTypes from './report.types';
import { RetrieveTransactionFromReport } from '../goaml-utils/goaml-utils';

export const setReport = item => ({
    type: ReportTypes.SET_REPORT,
    payload: item
});

export const addTransactionToReport = item => {
    return ({
        type: ReportTypes.ADD_REPORT_TRANSACTION,
        payload: item
    })
};

export const addTFrom = (report, internal_ref_number, item) => {
    const existingTxn = RetrieveTransactionFromReport(report, internal_ref_number);
    if (existingTxn) {
        existingTxn.t_from = item;
        let reportTxns = report.transaction.map(reportTransaction =>
            reportTransaction.internal_ref_number === internal_ref_number
                ? existingTxn
                : reportTransaction
        );
        return ({
            type: ReportTypes.T_FROM,
            payload: reportTxns
        })
    };
};

