import React from 'react';
import { useSelector } from 'react-redux';
// import usersData from '../../views/users/UsersData';
import CombinedTable from './combined-table.component';

const PersonTable = () => {
    console.log('im here');
    const fields = ['id', 'title','first_name', 'last_name', 'alias', 'ssn']

    const {personData} = useSelector(state => state.reportPersonReducer);
    // console.log('what', personData);

    return(
        <CombinedTable name="Persons Table" fields={fields} tableData={[personData]} />
    )
}

export default PersonTable;

