import {
    CCol, CInput,

    CLabel
} from '@coreui/react';
import React from 'react';
import './form-radio-text.style.css';

const FormRadioText = ({ label, name, value, checked, placeholder, width, items, handleChange }) => (
    <div className="form-group">
                {label ? (
                    <CLabel htmlFor={name}>{label}</CLabel>
                ) : null}
                <div className="row">
                    {items ? 
                    items.map((item) => {
                        return(
                            <CCol xs={width}>
                                <div className="input-group">
                                    <span className="input-group-addon">
                                        <input type="radio" onChange={handleChange} name={name} checked={checked} />
                                    </span>
                                    <CInput name={name} defaultValue={item} disabled />
                                </div>
                            </CCol>
                        )
                    }) : null}
                </div>
            </div>
  );

export default FormRadioText;