import CIcon from '@coreui/icons-react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFade,
    CForm,
    CFormGroup,
    CRow
} from '@coreui/react';
import React from 'react';
import transactionSignatory from 'src/redux/transaction-signatory/transaction-signatory.format';
import FormActions from '../../form-inputs/form-actions.component';
import FormRadioText from '../../form-inputs/form-radio-text.component';
import AccountPersonRoleEnum from '../../goaml-enums/account-person-role-enum/account-person-role-enum.component';
import TransactionPerson from '../transaction-person/transaction-person.component';

const SignatoryTemplate = ({ name, handlePhoneSave, ...otherProps }) => {

    const [collapsed, setCollapsed] = React.useState(false)
    const [showElements, setShowElements] = React.useState(true)
    const [componentState, setComponentState] = React.useState(transactionSignatory);

    const handleSaveClick = () => {
        handlePhoneSave(componentState);
    }

    const [check, setCheck] = React.useState(false);
    const onChecked = (e) => {
        const { name, value } = e.target;
        console.log({name, value})
        setCheck({[name]: value})
    }

    const handleChange = event => {
        const { name, value } = event.target;
        setComponentState({ ...componentState, [name]: value });
    };

    const handleCancelClick = () => {
        console.log("Hello: ", componentState);
    }
    
    return (
        <CRow>
            <CCol xs="12">
                <CFade timeout={300} in={showElements} unmountOnExit={true}>
                <CCard>
                    <CCardHeader>
                    {name}
                    <div className="card-header-actions">
                        <CButton 
                        color="link" 
                        className="card-header-action btn-minimize" 
                        onClick={() => setCollapsed(!collapsed)}
                        >
                        <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                        </CButton>
                    </div>
                    </CCardHeader>
                    <CCollapse show={collapsed} timeout={1000}>
                    <CCardBody>
                        <CForm className="form-horizontal">
                            <CFormGroup row>
                                <CCol xs="8">
                                    <FormRadioText 
                                        label="Is Primary?" 
                                        name="is_primary" 
                                        width="6" 
                                        items={["Yes", "No"]}
                                        handleChange={onChecked}
                                        defaultValue={componentState.is_primary}
                                    />
                                </CCol>
                                <CCol xs="4">
                                    <AccountPersonRoleEnum 
                                    label="Role" 
                                    name="role"
                                    handleChange={onChecked}
                                    defaultValue={componentState.role}
                                    ></AccountPersonRoleEnum>
                                </CCol>
                                <CCol xs="12">
                                    <TransactionPerson 
                                        name="person" 
                                        label="Person" 
                                        alias="Person"
                                        handleChange={onChecked}
                                        defaultValue={componentState.person}
                                    ></TransactionPerson>
                                </CCol>
                            </CFormGroup>
                            <FormActions 
                                saveText="Save Signatory" 
                                handleSaveClick={handleSaveClick} 
                                handleCancelClick={handleCancelClick}
                            ></FormActions>
                        </CForm>
                    </CCardBody>
                    </CCollapse>
                </CCard>
                </CFade>
            </CCol>
        </CRow>
    );
}

export default SignatoryTemplate;